@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
body{margin:0;padding:0;border:0}.highlight{transition:background-size 0.4s ease;background:linear-gradient(to bottom, transparent 62%, var(--alt-color) 0) center center/0% 75% no-repeat;font-size:2rem;padding:0 6px 2px 6px;cursor:pointer;color:var(--main-color);background-size:80% 100%;text-decoration:none}.highlight:hover{background-size:100% 100%}.button{border:1px solid black}@-webkit-keyframes cursor{0%{opacity:0}50%{opacity:100%}100%{opacity:0}}@keyframes cursor{0%{opacity:0}50%{opacity:100%}100%{opacity:0}}@-webkit-keyframes line{0%{transform-origin:0% 0%;transform:scale(1, 0)}50%{transform-origin:0% 0%;transform:scale(1, 1)}50.1%{transform-origin:0% 100%;transform:scale(1, 1)}100%{transform-origin:0% 100%;transform:scale(1, 0)}}@keyframes line{0%{transform-origin:0% 0%;transform:scale(1, 0)}50%{transform-origin:0% 0%;transform:scale(1, 1)}50.1%{transform-origin:0% 100%;transform:scale(1, 1)}100%{transform-origin:0% 100%;transform:scale(1, 0)}}.centered{text-align:center;justify-content:center}.flex{display:flex;width:100%}.flex-left{flex:0 0 50%}.flex-left p{padding:0 2rem}.flex-right{flex:0 0 50%;text-align:left}.flex-right p{padding:0 2rem}.cover{display:flex;flex-flow:column;height:100vh}.cover-logo{width:100%;align-self:center}.cover-logo-image img{width:50%}.cover-logo-text{font-size:140px}.cover-logo-text--arrow{letter-spacing:0.25rem}.cover-logo-text--slash{font-style:italic}.cover-logo-text--letter{position:relative;font-style:italic;padding:0 .5rem}.cover-logo-text--letter--cursor{position:absolute;left:0;bottom:1.5rem;width:100%;border-bottom:4px solid #ccc;padding:.15rem;-webkit-animation:cursor 1500ms infinite;animation:cursor 1500ms infinite}.cover-desc{flex-grow:1;align-items:center;text-align:right}.cover-bottom{flex-grow:0;font-size:40px;width:100%;display:flex;order:2;align-self:flex-end;justify-content:center}.cover-bottom-line{height:100px;justify-content:center;text-align:center;border-left:4px solid #ccc;-webkit-animation:line 1.2s infinite;animation:line 1.2s infinite}.cover-bottom-arrow{justify-content:center;text-align:center;margin-left:-17px;font-size:50px;transform:rotate(270deg)}@media only screen and (min-width: 600px){.cover-logo-image img{width:25%}}.about{display:flex;flex-flow:row;justify-content:center;height:100vh;align-items:center}.about-container{width:30%;margin:0 auto}.about-item{padding:1rem;border:1px solid #aaa}.about-item-body{font-size:20px}body{font-family:"Open Sans";font-size:20px;color:#ccc;background:#1b1b1b}

